// breakpoints (min-width == wider than : max-width == narrower than)

// occassional use for styles only less than 's'
@mixin bp-s-less {
  @media (max-width: #{$s - 0.01}) {
    @content;
  }
}

// styles greater than 's'
@mixin bp-s {
  @media (min-width: #{$s}) {
    @content;
  }
}

// occassional use for styles only less than 'm'
@mixin bp-m-less {
  @media (max-width: #{$m - 0.01}) {
    @content;
  }
}

// styles greater than 'm'
@mixin bp-m {
  @media (min-width: #{$m}) {
    @content;
  }
}

// occassional use for styles only less than 'l'
@mixin bp-l-less {
  @media (max-width: #{$l - 0.01}) {
    @content;
  }
}

// styles greater than 'l'
@mixin bp-l {
  @media (min-width: #{$l}) {
    @content;
  }
}

// styles greater than 'xl'
@mixin bp-xl {
  @media (min-width: #{$xl}) {
    @content;
  }
}

// occassional use for styles between 's' and 'm'
@mixin bp-s2m {
  @media (min-width: #{$s}) and (max-width: #{$m - 0.01}) {
    @content;
  }
}

// occassional use for styles between 'm' and 'l'
@mixin bp-m2l {
  @media (min-width: #{$m}) and (max-width: #{$l - 0.01}) {
    @content;
  }
}

// styles

@mixin clearfix() {
  display: table;
  clear: both;
  content: "";
}

@mixin hide-screen() {
  position: absolute;
  overflow: hidden;
  clip: rect(0,0,0,0);
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
}

@mixin lead() {
  font-weight: 300;
  font-size: $fs-22;
  @include bp-s {font-size: $fs-24;}
  @include bp-m {font-size: $fs-25;}
  @include bp-l {
    div.page-intro &,
    div.landing-intro & {font-size: $fs-40;}
  }
}

@mixin hide-input() {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
}

@mixin hide-label() {
  position: fixed;
  top: 50%; left: -1000em;
}
